.black-nav{
  background: black;
  width: 100%;
  display: flex;
  color: white;
  padding: 20px;
}
div{
  box-sizing: border-box;
  cursor: pointer;
}
.list{
  text-align: left;
  padding-left: 20px;
  border-bottom: 1px solid grey;
}

.modal{
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 30px;
  transform: translate(-50%,-50%);
  width: 80%;
  height: 50%;
  background-color: aliceblue;
}